body{
    background: radial-gradient(#ffff,#ffff,#ffff);
}

.card{
    width: 20rem;
}

.card:hover{
    box-shadow: 5px 10px 20px 1px rgb(0,0,0,0.253) !important;
}

.card-body{
    padding: 3rem 0 !important;
}

.card-text{
    font-size: 0.9rem;
    padding: 0.4rem 1.9rem;

}

.container-fluid .row{
    padding-top: 3rem;
}

.overflow{
    overflow: hidden;
}

.card-img-top{
    transform: scale(1);
    transition: transform 0.5s ease;
}

.card-img-top:hover{
    transform: scale(1.8);
}
.labelmodal{
    color: black;
    font-family: sans-serif
}
.p2{
    font-size: 10px;
    margin-left: 0px;
}